	 export default {
        name: 'switchComponent',
        props: {
			value: {
				type: Boolean,
				default: true
			},
			//背景颜色
			bgcolor: {
				type: String,
				default: "#1abc9c"
			},
			//宽度 px
			width: {
				type: Number,
				default: 52
			},
			text: {
				type: String,
				default: ''
			},
			sid: {
				type: Number,
				default: 0,
			}
        },
        data () {
            return {
                isChecked: this.value,
            }
        },
        computed: {
            direction () {
                if (this.text) {
                    return this.text.split('|')
                } else {
                    return []
                }
            }
        },
        watch: {
			value (val) {
				this.isChecked = val
			},
			isChecked(val) {
				let textTemp = '';
				if(val) {
					textTemp = this.text.split('|')[0];
				} 
				if(!val) {
					textTemp = this.text.split('|')[1];
				}
				let ob = {
					sid: this.sid,
					value: val,
					text: textTemp
				}
				this.$emit('change', ob);
			}
        },
        methods: {
          toggle(e) {
            this.isChecked = !this.isChecked;
          }
        }
    }   
